import ajax from '@/services/ajax';
import api from '@/services/api';

export function getMessagetIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getMessagetIndex );
}

export function getMessagetDetail(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getMessagetIndex );
}



